export function random_int32(seed, n) {
  n = n | 0;
  seed = seed;
  let rshift = (j, n2) => n2 ^ n2 >> j;
  let lshift = (j, n2) => n2 ^ n2 << j;
  n = n * 3039394381;
  n = n + seed;
  n = rshift(8, n);
  n = n + 1759714724;
  n = lshift(8, n);
  n = n * 458671337;
  n = rshift(8, n);
  return n;
}
export default function random(seed, n) {
  return ((random_int32(seed, n) & 1048320) >> 8) / 4095;
}
